import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { PostService } from '../../core/services/post.service';
import { Router } from '@angular/router';
import { PostInfo } from '../../components/shared/models/post-info';
import * as PostsActions from './actions';
import { map, switchMap, tap, catchError, takeUntil } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class PostEffects {

  constructor(
    private actions$: Actions,
    private postService: PostService,
    private router: Router
  ) { }

  allPosts$ = createEffect(() => this.actions$.pipe(
    ofType(PostsActions.getAllPosts),
    switchMap(() => {
      return this.postService.getAll().pipe(
        takeUntil(this.actions$.pipe(ofType(PostsActions.getAllPostsCancel))),
        map((posts: PostInfo[]) => PostsActions.getAllPostsSuccess({ posts })),
        catchError((err) => [PostsActions.getAllPostsFailed({ error: err.error })])
      );
    })
  ));

  allUserPosts$ = createEffect(() => this.actions$.pipe(
    ofType(PostsActions.getUserPosts),
    switchMap(() => {
      return this.postService.getUserPosts().pipe(
        map((userPosts: PostInfo[]) => PostsActions.getUserPostsSuccess({ posts: userPosts })),
        catchError((err) => [PostsActions.getUserPostsFailed({ error: err.error })])
      );
    })
  ));

  postById$ = createEffect(() => this.actions$.pipe(
      ofType(PostsActions.getPostById),
      switchMap(({ id }) => {
        return this.postService.getById(id).pipe(
          takeUntil(this.actions$.pipe(ofType(PostsActions.getPostByIdCansel))),
          map((post: PostInfo) => PostsActions.getPostByIdSuccess({ post })),
          catchError((err) => [PostsActions.getPostByIdFailed({ error: err.error })])
        );
      })
  ));

  createPost$ = createEffect(() => this.actions$.pipe(
    ofType(PostsActions.createPost),
    switchMap(({ body }) => {
      return this.postService.createPost(body).pipe(
        map(() => PostsActions.createPostSuccess({ })),
        tap(() => this.router.navigate(['/posts'])),
        catchError((err) => [PostsActions.createPostFailed({ error: err.error })])
      );
    })
  ));

  editPost$ = createEffect(() => this.actions$.pipe(
    ofType(PostsActions.editPost),
    switchMap(({ body, id }) => {
      return this.postService.editPost(body, id).pipe(
        map(() => PostsActions.editPostSuccess({ })),
        tap(() => this.router.navigate(['/posts'])),
        catchError((err) => [PostsActions.editPostFailed({ error: err.error })])
      );
    })
  ));

  postDelete$ = createEffect(() => this.actions$.pipe(
    ofType(PostsActions.deletePost),
    switchMap(({ id }) => {
      return this.postService.deletePost(id).pipe(
        map(() => {
          return PostsActions.deletePostSuccess({ });
        }),
        tap(() => {
          if (this.router.url !== '/posts') {
            // return PostsActions.getAllPosts({});
            this.router.navigate(['/posts']);
          }
        }),
        catchError((err) => [PostsActions.deletePostFailed({ error: err.error })])
      );
    })
  ));

}
