import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CommentInfo } from '../../components/shared/models/comment-info';

@Injectable({
  providedIn: 'root'
})

export class CommentService {
  private readonly BASE_URL = 'http://localhost:3000/comments';

  constructor(private http: HttpClient) { }

  getAllForPost(postId: string) {
    return this.http.get<CommentInfo[]>(`${this.BASE_URL}?postId=${postId}`, { withCredentials: true });
  }

  createComment(body: any) {
    return this.http.post(this.BASE_URL, body, { withCredentials: true });
  }

  deleteComment(id: string) {
    return this.http.delete(`${this.BASE_URL}/${id}`, { withCredentials: true });
  }
}
