import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivateChild,
  Router,
  RouterStateSnapshot
} from '@angular/router';
import { Store } from '@ngrx/store';
import { IAppState, getAuthUser } from '../../+store';
import { AuthService } from '../services/auth.service';
import { Observable } from 'rxjs';
import { map, tap, switchMap } from 'rxjs/operators';
import * as AuthActions from '../../+store/auth/actions';

@Injectable({
  providedIn: 'root'
})

export class AuthGuard implements CanActivateChild {

  user$: Observable<{}>;

  constructor(
    private store: Store<IAppState>,
    private authService: AuthService,
    private router: Router
  ) { }

  canActivateChild(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    this.user$ = this.store.select(getAuthUser);
    const user_name = localStorage.getItem('username');
    if (!user_name){ return this.router.navigate(['/login']); }
    return this.user$.pipe(
      switchMap(user => user === undefined || null ? this.authService.getUser(user_name) : [user]),
      map((user) => {
        this.store.dispatch(AuthActions.setAuth({ auth: (user !== undefined || null) }));
        this.store.dispatch(AuthActions.setUser({ user }));
        const isLoggedFromData = childRoute.data.isLogged;
        return typeof isLoggedFromData !== 'boolean' || isLoggedFromData === !!user;
      }),
      tap((canContinue) => {
        if (canContinue) { return; }
        const url = this.router.url;
        this.router.navigateByUrl(url);
      })
    );
  }
}
