import { createAction, props } from '@ngrx/store';
import { CommentInfo } from '../../components/shared/models/comment-info';

export const ActionTypes = {
  GetAll: '[COMMENT] GetAll',
  GetAllSuccess: '[COMMENT] GetAllSuccess',
  GetAllFailed: '[COMMENT] GetAllFailed',

  CreateComment: '[COMMENT] CreateComment',
  CreateCommentSuccess: '[COMMENT] CreateCommentSuccess',
  CreateCommentFailed: '[COMMENT] CreateCommentFailed',

  DeleteComment: '[COMMENT] DeleteComment',
  DeleteCommentSuccess: '[COMMENT] DeleteCommentSuccess',
  DeleteCommentFailed: '[COMMENT] DeleteCommentFailed'
};


export const getAllComments = createAction(
  ActionTypes.GetAll,
  props<{ id: string }>()
);
export const getAllCommentsSuccess = createAction(
  ActionTypes.GetAllSuccess,
  props<{ comments: CommentInfo[] }>()
);
export const getAllCommentsFailed = createAction(
  ActionTypes.GetAllFailed,
  props<{ error: any }>()
);


export const createComment = createAction(
  ActionTypes.CreateComment,
  props<{ body: any }>()
);
export const createCommentSuccess = createAction(
  ActionTypes.CreateCommentSuccess,
  props<{ }>()
);
export const createCommentFailed = createAction(
  ActionTypes.CreateCommentFailed,
  props<{ error: any }>()
);


export const deleteComment = createAction(
  ActionTypes.DeleteComment,
  props<{ id: string }>()
);
export const deleteCommentSuccess = createAction(
  ActionTypes.DeleteCommentSuccess,
  props<{ }>()
);
export const deleteCommentFailed = createAction(
  ActionTypes.DeleteCommentFailed,
  props<{ error: any }>()
);

