import { createReducer, on } from '@ngrx/store';
import * as CommentsActions from './actions';
import { CommentInfo } from '../../components/shared/models/comment-info';

export interface ICommentState {
  all: CommentInfo[];
  errorMessage: string;
}

const initialState: ICommentState = {
  all: [],
  errorMessage: null
};

export const reducer = createReducer(
  initialState,
  on(CommentsActions.getAllCommentsSuccess, (state, { comments }) => {
    return { ...state, all: comments };
  }),
  on(CommentsActions.getAllCommentsFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(CommentsActions.createCommentFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(CommentsActions.deleteCommentFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  })
);
