import { createReducer, on } from '@ngrx/store';
import * as PostsActions from './actions';
import { PostInfo } from '../../components/shared/models/post-info';

export interface IPostState {
  all: PostInfo[];
  post: PostInfo;
  userPosts: PostInfo[];
  errorMessage: string;
}

const initialState: IPostState = {
  all: [],
  post: null,
  userPosts: [],
  errorMessage: null
};

export const reducer = createReducer(
  initialState,
  on(PostsActions.getAllPostsSuccess, (state, { posts }) => {
    return { ...state, all: posts };
  }),
  on(PostsActions.getAllPostsFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(PostsActions.getPostByIdSuccess, (state, { post }) => {
    return { ...state, post };
  }),
  on(PostsActions.getPostByIdFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(PostsActions.getUserPostsSuccess, (state, { posts }) => {
    return { ...state, userPosts: posts };
  }),
  on(PostsActions.getUserPostsFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(PostsActions.createPostFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(PostsActions.editPostFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(PostsActions.deletePostFailed, (state, { error }) => {
    return {...state, errorMessage: error };
  })
);
