import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  private readonly BASE_URL = `http://localhost:3000/users`;
  // private options = { withCredentials: true };

  constructor(private http: HttpClient) { }

  signUp(body: { username: string, password: string }) {
    return this.http.post(`${this.BASE_URL}/register`, body);
  }

  signIn(body: { username: string, password: string }) {
    return this.http.post<{ username: string; _id: string }>(`${this.BASE_URL}/login`, body, { withCredentials: true });
  }

  logout() {
    return this.http.post(`${this.BASE_URL}/logout`, {}, { withCredentials: true });
  }

  getUser(username: string) {
    return this.http.get(`${this.BASE_URL}/${username}`);
  }
}
