import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { CommentService } from '../../core/services/comment.service';
import { CommentInfo } from '../../components/shared/models/comment-info';
import * as CommentsActions from './actions';
import { map, switchMap, mergeMap, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class CommentsEffects {
  constructor(
    private actions$: Actions,
    private commentService: CommentService
  ) { }

  allUserComments$ = createEffect(() => this.actions$.pipe(
    ofType(CommentsActions.getAllComments),
    switchMap(({ id }) => {
      return this.commentService.getAllForPost(id).pipe(
        map((comments: CommentInfo[]) => {
          return CommentsActions.getAllCommentsSuccess({ comments });
        }),
        catchError((err) => [CommentsActions.getAllCommentsFailed({ error: err.error })])
      );
    })
  ));

  createComment$ = createEffect(() => this.actions$.pipe(
    ofType(CommentsActions.createComment),
    switchMap(({ body }) => {
      return this.commentService.createComment(body).pipe(
        map(() => CommentsActions.createCommentSuccess({ })),
        catchError((err) => [CommentsActions.createCommentFailed({ error: err.error })])
      );
    })
  ));

  deleteComment$ = createEffect(() => this.actions$.pipe(
    ofType(CommentsActions.deleteComment),
    switchMap(({ id }) => {
      return this.commentService.deleteComment(id).pipe(
        map(() => CommentsActions.deleteCommentSuccess({ })),
        catchError((err) => [CommentsActions.deleteCommentFailed({ error: err.error })])
      );
    })
  ));
}
