import { createAction, props } from '@ngrx/store';

export const ActionTypes = {
  Register: '[AUTH] Register',
  RegisterSuccess: '[AUTH] Register Success',
  RegisterFailed: '[AUTH] Register Failed',

  Login: '[AUTH] Login',
  LoginSuccess: '[AUTH] Login Success',
  LoginFailed: '[AUTH] Login Failed',

  Logout: '[AUTH] Logout',
  LogoutSuccess: '[AUTH] Logout Success',
  LogoutFailed: '[AUTH] Logout Failed',

  SetAuth: '[AUTH] Set Auth',
  SetUser: '[AUTH] Set User'
};

export const register = createAction(
  ActionTypes.Register,
  props<{ username: string, password: string }>()
);
export const registerSuccess = createAction(
  ActionTypes.RegisterSuccess,
  props<{}>()
);
export const registerFailed = createAction(
  ActionTypes.RegisterFailed,
  props<{ error: any }>()
);


export const login = createAction(
  ActionTypes.Login,
  props<{ username: string, password: string }>()
);
export const loginSuccess = createAction(
  ActionTypes.LoginSuccess,
  props<{ username: string, userId: string }>()
);
export const loginFailed = createAction(
  ActionTypes.LoginFailed,
  props<{ error: any }>()
);


export const logout = createAction(
  ActionTypes.Logout,
  props<{}>()
);
export const logoutSuccess = createAction(
  ActionTypes.LogoutSuccess,
  props<{}>()
);
export const logoutFailed = createAction(
  ActionTypes.LogoutFailed,
  props<{ error: any }>()
);


export const setAuth = createAction(
  ActionTypes.SetAuth,
  props<{ auth: boolean }>()
);

export const setUser = createAction(
  ActionTypes.SetUser,
  props<{ user: any }>()
);


