import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PostInfo } from '../../components/shared/models/post-info';
import { Store } from '@ngrx/store';
import { IAppState, getAuthUsername } from '../../+store';

@Injectable({
  providedIn: 'root'
})

export class PostService {
  private readonly BASE_URL = `http://localhost:3000/posts`;
  private readonly BASE_USERS_URL = `http://localhost:3000/users`;
  authorName: string;

  constructor(
    private http: HttpClient,
    private store: Store<IAppState>
  ) {
    this.store.select(getAuthUsername).subscribe((username) => {
      if (!username) {
        this.authorName = localStorage.getItem('username');
        return;
      }
      this.authorName = username;
    });
  }

  getAll() {
    return this.http.get<PostInfo[]>(this.BASE_URL, { withCredentials: true });
  }

  getById(id: string) {
    return this.http.get<PostInfo>(this.BASE_URL + `/${id}`, { withCredentials: true });
  }

  getUserPosts() {
    return this.http.get<PostInfo[]>(
      `${this.BASE_USERS_URL}/${this.authorName}/posts`, { withCredentials: true }
    );
    // `${this.BASE_URL}/user?author=${this.authorName}`
    // author=${localStorage.getItem('username')}
    // query={"author":"${localStorage.getItem('username')}"}
  }

  getDetails(id: string) {
    return this.http.get<PostInfo>(this.BASE_URL + `/${id}/details`, { withCredentials: true });
    // `/details/${id}`
  }

  createPost(body: any) {
    return this.http.post(this.BASE_URL, body, { withCredentials: true });
    // `/create`
  }

  editPost(body: any, id: string) {
    return this.http.put(this.BASE_URL + `/${id}`, body, { withCredentials: true });
    // `/edit/${id}`
  }

  deletePost(id: string) {
    return this.http.delete(this.BASE_URL + `/${id}`, { withCredentials: true });
    // `/delete/${id}`
  }
}
