import { createAction, props } from '@ngrx/store';
import { PostInfo } from '../../components/shared/models/post-info';

export const ActionTypes = {
  GetAll: '[POST] GetAll',
  GetAllSuccess: '[POST] GetAllSuccess',
  GetAllFailed: '[POST] GetAllFailed',
  GetAllCancel: '[POST] GetAllCancel',

  GetPostById: '[POST] GetPostById',
  GetPostByIdSuccess: '[POST] GetPostByIdSuccess',
  GetPostByIdFailed: '[POST] GetPostByIdFailed',
  GetPostByIdCancel: '[POST] GetPostByIdCancel',

  GetUserPosts: '[POST] GetUserPosts',
  GetUserPostsSuccess: '[POST] GetUserPostsSuccess',
  GetUserPostsFailed: '[POST] GetUserPostsFailed',

  CreatePost: '[POST] CretePost',
  CreatePostSuccess: '[POST] CreatePostSuccess',
  CreatePostFailed: '[POST] CreatePostFailed',

  EditPost: '[POST] EditPost',
  EditPostSuccess: '[POST] EditPostSuccess',
  EditPostFailed: '[POST] EditPostFailed',

  DeletePost: '[POST] DeletePost',
  DeletePostSuccess: '[POST] DeletePostSuccess',
  DeletePostFailed: '[POST] DeletePostFailed'
};

export const getAllPosts = createAction(
  ActionTypes.GetAll,
  props<{ }>()
);
export const getAllPostsSuccess = createAction(
  ActionTypes.GetAllSuccess,
  props<{ posts: PostInfo | any }>()
);
export const getAllPostsFailed = createAction(
  ActionTypes.GetAllFailed,
  props<{ error: any }>()
);
export const getAllPostsCancel = createAction(
  ActionTypes.GetAllCancel
);


export const getPostById = createAction(
  ActionTypes.GetPostById,
  props<{ id: string }>()
);
export const getPostByIdSuccess = createAction(
  ActionTypes.GetPostByIdSuccess,
  props<{ post: PostInfo }>()
);
export const getPostByIdFailed = createAction(
  ActionTypes.GetPostByIdFailed,
  props<{ error: any }>()
);
export const getPostByIdCansel = createAction(
  ActionTypes.GetPostByIdCancel
);


export const getUserPosts = createAction(
  ActionTypes.GetUserPosts,
  props<{ }>()
);
export const getUserPostsSuccess = createAction(
  ActionTypes.GetUserPostsSuccess,
  props<{ posts: PostInfo | any }>()
);
export const getUserPostsFailed = createAction(
  ActionTypes.GetUserPostsFailed,
  props<{ error: any }>()
);



export const createPost = createAction(
  ActionTypes.CreatePost,
  // tslint:disable-next-line:ban-types
  props<{ body: Object }>()
);
export const createPostSuccess = createAction(
  ActionTypes.CreatePostSuccess,
  props<{ }>()
);
export const createPostFailed = createAction(
  ActionTypes.CreatePostFailed,
  props<{ error: any }>()
);


export const editPost = createAction(
  ActionTypes.EditPost,
  // tslint:disable-next-line:ban-types
  props<{ id: string, body: Object }>()
);
export const editPostSuccess = createAction(
  ActionTypes.EditPostSuccess,
  props<{ }>()
);
export const editPostFailed = createAction(
  ActionTypes.EditPostFailed,
  props<{ error: any }>()
);


export const deletePost = createAction(
  ActionTypes.DeletePost,
  props<{ id: string }>()
);
export const deletePostSuccess = createAction(
  ActionTypes.DeletePostSuccess,
  props<{ }>()
);
export const deletePostFailed = createAction(
  ActionTypes.DeletePostFailed,
  props<{ error: any }>()
);
