import { ActionReducerMap } from '@ngrx/store';
import { IState as IAuthState, reducer as authReducer } from './auth/reducers';
import { IPostState, reducer as postsReducer } from './posts/reducers';
import { ICommentState, reducer as commentsReducer } from './comments/reducers';
import { createFeatureSelector, createSelector } from '@ngrx/store';
import { routerReducer, RouterReducerState } from '@ngrx/router-store';
import { RouterStateUrl } from '../core/app-router-serializer';
import * as authSel from './auth/selectors';
import * as posts from './posts/selectors';
import * as comments from './comments/selectors';

export interface IAppState {
  auth: IAuthState;
  posts: IPostState;
  comments: ICommentState;
  router: RouterReducerState<RouterStateUrl>;
}

export const reducers: ActionReducerMap<IAppState> = {
  auth: authReducer,
  posts: postsReducer,
  comments: commentsReducer,
  router: routerReducer
};

/* Auth Selectors */
export const getAuthStore = createFeatureSelector('auth');
export const getAuthUsername = createSelector(getAuthStore, authSel.getUsername);
export const getAuthUserId = createSelector(getAuthStore, authSel.getUserId);
export const getIsAuth = createSelector(getAuthStore, authSel.getAuth);
export const getAuthUser = createSelector(getAuthStore, authSel.getUser);
export const getAuthErrorMessage = createSelector(getAuthStore, authSel.getErrorMessage);
// export const getIsAuthenticated = createSelector(getIsAuth, auth => !! auth);

/* Post Selectors */
export const getPostStore = createFeatureSelector('posts');
export const getPostAll = createSelector(getPostStore, posts.getAll);
export const getByIdPost = createSelector(getPostStore, posts.getById);
export const getPostsUser = createSelector(getPostStore, posts.getPosts);
export const getPostErrorMessage = createSelector(getPostStore, posts.getErrorMessage);

/* Comments Selectors */
export const getCommentsStore = createFeatureSelector('comments');
export const getCommentAll = createSelector(getCommentsStore, comments.getAll);
export const getCommentErrorMessage = createSelector(getCommentsStore, comments.getErrorMessage);

/* Router Selectors */
export const getRouterStore = createFeatureSelector('router');
export const getRouterUrl = createSelector(getRouterStore,
  (routerState: RouterReducerState<RouterStateUrl>) => routerState ? routerState.state.url : '');
