import { Injectable } from '@angular/core';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { AuthService } from '../../core/services/auth.service';
import { Router } from '@angular/router';
import * as AuthActions from './actions';
import { map, switchMap, mergeMap, tap, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class AuthEffects {

  constructor(
    private actions$: Actions,
    private authService: AuthService,
    private router: Router
  ) { }

  register$ = createEffect(() => this.actions$.pipe(
    ofType(AuthActions.register),
    switchMap(({ username, password }) => {
      return this.authService.signUp({ username, password }).pipe(
        map(() => {
          return AuthActions.registerSuccess({});
        }),
        tap(() => this.router.navigate(['/login'])),
        catchError((err) => [AuthActions.registerFailed({ error: err.error })])
      );
    })
  ));

  login$ = createEffect(() => this.actions$.pipe(
    ofType(AuthActions.login),
    switchMap(({ username, password }) => {
      return this.authService.signIn({ username, password }).pipe(
        tap((user) => localStorage.setItem('username', user.username)),
        mergeMap((user) => {
          return [
            AuthActions.loginSuccess({ username: user.username, userId: user._id }),
            AuthActions.setAuth({ auth: (user !== undefined || null) }),
            AuthActions.setUser({ user })
          ];
        }),
        tap((res) => {
          // console.log(res);
          // localStorage.setItem('username', username);
          this.router.navigate(['/posts']);
        }),
        catchError((err) => [AuthActions.loginFailed({ error: err.error })])
      );
    })
  ));

  logout$ = createEffect(() => this.actions$.pipe(
    ofType(AuthActions.logout),
    switchMap(({}) => {
      return this.authService.logout().pipe(
        map(() => {
          return AuthActions.logoutSuccess({});
        }),
        tap(() => {
          localStorage.clear();
          this.router.navigate(['/login']);
        }),
        catchError((err) => {
          // console.log(err.error.error.message);
          return [AuthActions.logoutFailed({ error: err.error })];
        })
      );
    })
  ));

}



