import { createReducer, on } from '@ngrx/store';
import * as AuthActions from './actions';

export interface IState {
  userId: string;
  username: string;
  auth: boolean;
  errorMessage: string;
  currentUser: Object | undefined;
}

const initialState: IState = {
  userId: '',
  username: '',
  auth: false,
  errorMessage: '',
  currentUser: undefined
};

export const reducer = createReducer<IState>(
  initialState,
  on(AuthActions.setAuth, (state, { auth }) => {
    return { ...state, auth };
  }),
  on(AuthActions.registerFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(AuthActions.loginSuccess, (state, { username, userId }) => {
    return { ...state, username, userId };
  }),
  on(AuthActions.loginFailed, (state, { error }) => {
    return { ...state, errorMessage: error };
  }),
  on(AuthActions.setUser, (state, action ) => {
    return { ... state, currentUser: action.user, userId: action.user._id };
  }),
  on(AuthActions.logoutSuccess, () => initialState)
);
